.footer {
  background: linear-gradient(135deg, #4a1a7e 0%, #1a0a3e 100%);
  color: white;
  padding: 60px 0 0 0;
}

.footer-section {
  max-width: 1300px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
  padding: 0 20px 40px 20px;
}

.footer-about-us__box {
  margin-bottom: 20px;
}

.footer-about-us__logo {
  width: 150px;
}

.footer-about-us__title {
  font-size: 22px;
  margin-bottom: 15px;
  font-weight: 600;
}

.footer-about-us__text {
  display: block;
  line-height: 1.6;
  color: #d0d0d0;
  margin-bottom: 25px;
}

.footer-about-us__social-media {
  display: flex;
  gap: 10px;
}

.footer-about-us__icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #a855f7;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  transition: background 0.3s;
}

.footer-about-us__icon:hover {
  background: #9333ea;
}

.footer-useful-likes_list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-useful-likes__title {
  font-size: 22px;
  margin-bottom: 20px;
  font-weight: 600;
}

.footer-useful-likes__item {
  margin-bottom: 12px;
}

.footer-useful-likes__link {
  color: #d0d0d0;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-useful-likes__link:hover {
  color: #a855f7;
}

.footer-contact__title {
  font-size: 22px;
  margin-bottom: 20px;
  font-weight: 600;
}

.footer-contact__location,
.footer-contact__email,
.footer-contact__phone {
  display: flex;
  align-items: flex-start;
  gap: 15px;
  margin-bottom: 20px;
}

.footer-contact__location-icon,
.footer-contact__email-icon,
.footer-contact__phone-icon {
  color: #a855f7;
  font-size: 18px;
  margin-top: 3px;
}

.footer-contact__location-address,
.footer-email__address,
.footer-phone__number {
  color: #d0d0d0;
  line-height: 1.6;
}

.footer-gallery__title {
  font-size: 22px;
  margin-bottom: 20px;
  font-weight: 600;
}

.footer-gallery__photos {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.footer-gallery__img {
  width: 100%;
  height: 80px;
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.3s;
}

.footer-gallery__img:hover {
  transform: scale(1.05);
}

.footer-copyright {
  background: #0a0520;
  text-align: center;
  padding: 20px;
}

.footer-copyright__text {
  color: #d0d0d0;
  font-size: 17px;
}